<template lang="pug">
    .result-wrapper(v-if="status.resultBlcok")
        .result-head
            .result-info Showing #[span.result-num {{resultData.total}}] results by…
            .result-tags
                .tag-item Taipei
                    i.far.fa-times-circle
        .result-body
            .result-list
                .result-item.card(:key="index" v-for="(item,index) in resultData.data")
                    .item-img.card-img(:style="{'background':'url('+item.img+') #D7D7D7'}")
                    .item-right.card-info
                        h2.title {{item.title}}
                        p.desc {{item.desc}}
                        .more-info
                            .organizer {{item.organizer}}
                            .tags(:key="index" v-for="(tag,index) in item.tags")
                                .tag {{tag}}
                        .more-info
                            .location {{item.location}}
                            .date {{item.date}}
</template>

<script>
export default {
    name: 'resultBlock',
    props: ['status','resultData'],
    data(){
        return{
            
        }
    }
}
</script>





